<template>
    <div id="container">
        <v-card max-width="1000px" class="mx-auto mt-9">
                <v-card-title>
                    <h3>Cek Stok Per Item</h3>
                </v-card-title>
                <v-card-text>
                    <v-container >
                        <v-row no-gutters>
                            <v-col cols="2">
                                <h3 class="mt-2"> Kode Barang</h3>
                            </v-col>
                            <v-col cols="3">
                                <v-autocomplete dense single-line outlined :items="TblStok" item-text="Kode Barang" item-value="Kode Barang" label="Kode Barang" class="title mr-4" v-model="kodeBarang" @change="getDataPerItem()">
                                    <template slot="item" slot-scope="data">
                                        {{ data.item["Kode Barang"] }} - {{ data.item["Nama Barang"] }}
                                    </template>
                                </v-autocomplete>
                            </v-col>
                            <v-col cols="1"></v-col>
                            <v-col cols="2">
                                <h3 class="mt-2">Nama Barang</h3>
                            </v-col>
                            <v-col cols="3">
                                <v-text-field dense single-line outlined label="Nama Barang" class="title mr-4" v-model="dataAtas['Nama Barang']" readonly>
                                </v-text-field>
                            </v-col>
                            <v-col cols="1"></v-col>
                            <v-col cols="2">
                                <h3 class="mt-2">Sisa Awal</h3>
                            </v-col>
                            <v-col cols="3">
                                <v-text-field dense single-line outlined label="Sisa Awal" class="title mr-4" v-model="dataAtas['Sisa Awal']" readonly>
                                </v-text-field>
                            </v-col>
                            <v-col cols="1"></v-col>
                            <v-col cols="2">
                                <h3 class="mt-2">Sisa Akhir</h3>
                            </v-col>
                            <v-col cols="3">
                                <v-text-field dense single-line outlined label="Sisa Akhir" class="title mr-4" v-model="sisaAkhir" readonly>
                                </v-text-field>
                            </v-col>
                            <v-col cols="1"></v-col>
                        </v-row>
                        <v-data-table :headers="headers" :items="anak" :search="keyword" :loading="load" class="overflow-y-auto" style="max-height: 400px">
                        <template v-slot:body="{ items }">
                            <tbody>
                                <tr v-for="(item) in items" :key='item["id"]'>
                                    <td class="title">{{ item["Tanggal"] }}</td>
                                    <td class="title">{{ item["No Bukti"] }}</td>
                                    <td class="title">{{ item["Keterangan"] }}</td>
                                    <td class="title">{{ item["Masuk"] }}</td>
                                    <td class="title">{{ item["Keluar"] }}</td>
                                </tr>
                            </tbody>
                        </template>
                        </v-data-table>
                    </v-container>
                </v-card-text>
            </v-card>
    </div>
</template>
<script>
export default {
    data() {
            return {
                TblStok: [],
                induk:[],
                anak: [],
                dataAtas: [],
                kodeBarang: '',
                sisaAkhir: '',
                snackbar: false,
                color: null,
                text: '',
                load: false,
                keyword: '',
                headers: [
                    {
                        text: 'Tanggal',
                        value: 'Tanggal',
                        class: 'title',
                    },
                    {
                        text: 'No Bukti',
                        value: 'No Bukti',
                        class: 'title',
                    },
                    {
                        text: 'Keterangan',
                        value: 'Keterangan',
                        class: 'title',
                    },
                    {
                        text: 'Masuk',
                        value: 'Masuk',
                        class: 'title',
                    },
                    {
                        text: 'Keluar',
                        value: 'Keluar',
                        class: 'title',
                    },
                ],
            }
        },
        methods: {
             getDataTblStok() {
                var uri = this.$apiUrl + 'tblstoks'
                this.$http.get(uri).then(response => {
                    this.TblStok = response.data
                })
            },

            getDataPerItem(){
                var uri = this.$apiUrl + 'produksis/cekstokperitem/' + this.kodeBarang
                this.$http.get(uri).then(response => {
                    this.induk = response.data[0][0]
                    this.anak = response.data[1]
                    this.dataAtas = response.data[2][0]
                    this.sisaAkhir = this.dataAtas["Sisa Awal"] + (this.induk["SumOfMasuk"] - this.induk["SumOfKeluar"])
                })

            },

            close() {
                this.dialog = false
                this.dialogEdit = false
                this.resetForm()
                this.typeInput = 'new'
            },
            resetForm() {
                this.form = {
                    tanggal: '',
                    noSuplier: '',
                    noBukti: '',
                    jumlahRupiah: 0,
                    potongan: 0,
                    pembulatan: 0,
                    pembayaran: 0,
                    ppn: 0,
                    name: '',
                    kodePerkiraanPembayaran: '',
                    status: '',
                    noFaktur:'',
                }
                this.dbelis= [{
                    kodeBarang: '',
                    jumlah: 0,
                    harga: 0,
                    subTotal:0,
                    noLP: '',
                    noBet: '',
                }],
                this.statusUpdate = true
            }
        },
        mounted() {
            this.getDataTblStok();
        },
}
</script>